import React, { Component } from 'react';
import { SingleProduct } from '../Products/Products';
import '../../styles/flashsale.css';

const flashsales = [
    {
        image: 'assets/images/1.png',
        href: '#',
        stars: Math.random() * 2 + 3,
        numberOfRatings: Math.random() * 300 + 10,
        name: 'SPECIAL 001',
        salePrice: 1190000,
        originPrice: 2190000
    }, {
        image: 'assets/images/2.png',
        href: '#',
        stars: Math.random() * 2 + 3,
        numberOfRatings: Math.random() * 300 + 10,
        name: 'SPECIAL 002',
        salePrice: 1190000,
        originPrice: 2190000
    }, {
        image: 'assets/images/3.png',
        href: '#',
        stars: Math.random() * 2 + 3,
        numberOfRatings: Math.random() * 300 + 10,
        name: 'SPECIAL 003',
        salePrice: 1190000,
        originPrice: 2190000
    }, {
        image: 'assets/images/4.png',
        href: '#',
        stars: Math.random() * 2 + 3,
        numberOfRatings: Math.random() * 300 + 10,
        name: 'SPECIAL 004',
        salePrice: 1190000,
        originPrice: 2190000
    }
];


class FlashSale extends Component {
    render() {
        return(
            <div className="flash-sale-container">
                <div className="fs-header">
                    <img src="assets/icons/flash.png" />
                    <span>FLASH SALE TỪ NGÀY 15/03 ĐẾN 17/03</span>
                    <img src="assets/icons/flash.png" />
                </div>
                <div className="products-container">
                {
                    flashsales.map(item =>
                        <SingleProduct image={item.image}
                            href={item.href}
                            name={item.name}
                            salePrice={item.salePrice}
                            originPrice={item.originPrice} 
                            stars={item.stars}
                            numberOfRatings={item.numberOfRatings} />    
                    )
                }
                </div>
            </div>
        );
    };
};

export default FlashSale;